<template>
    <div class="page-login">
       <div class="login-header">
           <router-link class="logo" :to="{name: 'index'}"></router-link>
       </div>
       <div class="login-panel">
           <div class="banner">
               <img src="http://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" width="480" height="370" alt="">
           </div>
           <div class="form">
                <div v-if="error" class="tips">
                    <i></i>
                    {{ error }}
                </div>
                <p><span></span></p>
                <el-input v-model="userName" placeholder="//">
                    <i slot="prefix" class="el-input__icon profile"></i>
                </el-input>
                <el-input v-model="password" placeholder="" type="password">
                    <i slot="prefix" class="el-input__icon password"></i>
                </el-input>
                <p class="foot">
                    <a href="#">?</a>
                </p>
                <el-button class="btn-login" @click="submit"></el-button>
                <p class="reg">
                    <span>?</span>
                    <router-link :to="{name: 'register'}"></router-link>
                </p>
                <div class="oauth-wrapper">
                    <div class="title-wrapper"><span class="title"></span></div>
                    <div class="oauth">
                        <a class="oauth__link oauth__link--qq" href="#"></a>
                        <a class="oauth__link oauth__link--weibo" href="#"></a>
                    </div>
                </div>
           </div>
       </div>
       <footer>
           <ul>
               <li><a href="#"></a></li>
               <li><a href="#"></a></li>
               <li><a href="#"></a></li>
               <li><a href="#"></a></li>
               <li><a href="#"></a></li>
           </ul>
           <p>2018  meituan.com ICP070791 11010502025545</p>
       </footer>
    </div>
</template>

<script>
import api from '@/api/api.js'

export default {
    data() {
        return {
            userName: '',
            password: '',
            error: ''
        }
    },
    methods: {
        submit() {
            //   
            //    
            //       
            //  
            if(!this.userName) {
                if(!this.password) {
                    this.error = ''
                    return
                }
                this.error = '';
                return
            }else if(!this.password) {
                this.error = '';
                return
            }
            // 
            api.login({
                params: {
                    userName: this.userName,
                    password: this.password
                }
            }).then(res => {
                // console.log(res.data);
                const data = res.data;
                //     
                if(data.status === 'success') {
                    this.$store.commit('setUserName', data.data);
                    this.$router.push({name: 'index'});
                //  
                }else if (data.status === "fail") {
                    this.error = data.msg;
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
    @import '../assets/css/login/login.less';
</style>


