<template>
  <div class="wrapper">
    <div class="alphabet">
      <h3>:</h3>
      <span class="letter" v-for="(item, index) in alphabetList" :key="item + '' + index">
        <a :href="'#city-' + item">{{ item }}</a>
      </span>
    </div>
    <dl :id="'city-' + index2" class="category-wrapper" v-for="(item2, index2) in category" :key="item2 + '' + index2">
      <dt>{{ index2 }}</dt>
      <dd >
          <router-link :to="{name: 'index', params: { position: city.name }}" v-for="(city, index3) in item2" :key="city + '&' + index3">
            {{ city.name }}
          </router-link>
      </dd>
    </dl>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      alphabetList: "ABCDEFJHJKLMNPQRSTWXYZ".split(""),
      category: null
    };
  },
  props: ["cityList"],
  methods: {
    //   {a:[], b:[], c:[]}
    handleData() {
      let obj = {};
      let data = this.cityList;
      
      // 
      // if(!data[0]) {
      //   return;
      // }else {
      //   clearTimeout(this.timer);
      // }

      // console.log(data)
      //  
      data.forEach((item, index) => {
        if (!obj[item.firstChar]) {
          obj[item.firstChar] = [];
        }
        obj[item.firstChar].push(item);
      });
      this.resetObj(obj);
    },
    resetObj(obj) {
        let lastObj = {};
        let alphabet = this.alphabetList;
        // 
        alphabet.forEach(letter => {
            lastObj[letter] = obj[letter];
        });
        this.category = lastObj;
    }
        
  },
  watch: {
    cityList: function () {
      this.handleData()
    }
  }
  // created() {
  //   //cityList
  //   this.timer = setInterval(() => {
  //     this.handleData();
  //     console.log('666');
  //   }, 200);
  // }
};

/* 
   data data  :
    
  changecity
   category[]
     category
   created
   created 
   changecity 
     handleData()  category

  : 
  1. 
   cityList    handleData()   category

  2. 
  created,   handleData() 
   handleData()   :
     category
   return   
*/
</script>

<style lang="less" scoped>
@import "../../assets/css/changecity/category.less";
</style>


